as buenas prácticas de cuidado, limpieza y protección en las instituciones educativas. A través de metodologías participativas, análisis de casos y actividades prácticas, se busca promover un aprendizaje activo que facilite la aplicación de las herramientas y conceptos en la realidad escolar, permitiendo adoptar medidas preventivas y correctivas que aseguren un ambiente seguro y saludable para todos.")])])]),_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/intro/3.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"bg-color-2 p-5",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("La unidad se estructura en tres módulos principales: el primero, \"Conceptos Generales de Higiene y Seguridad en el Trabajo con Infantes\", ofrece una visión clara acerca de los principios básicos que sustentan la protección en el entorno escolar, incluyendo definiciones, normativas vigentes y las responsabilidades del personal; el segundo, \"Normas y Buenas Prácticas en la Atención a Infantes\", profundiza en las recomendaciones de higiene personal, manipulación de alimentos, limpieza de espacios y manejo de materiales, así como en la prevención de accidentes y enfermedades transmisibles; y el tercero, \"Implementación del Sistema de Gestión de la Seguridad y Salud en el Trabajo (SGSST)\", presenta los elementos esenciales para desarrollar, implementar y mantener un sistema efectivo que garantice condiciones seguras, incluyendo la identificación de peligros, evaluación de riesgos, planes de emergencia y capacitación del personal.")])])])])])}]

